

















import Vue from 'vue'
import { mapState } from 'vuex'
import { MessageI } from '@/interface/model'

export default Vue.extend({
  name: 'profile',
  data () {
    return {
    }
  },
  computed: {
    ...mapState(['user', 'message']),
    isReady () {
      return this.user === null
    },
    msgCnt () {
      const tmp = this.message.filter((item: MessageI) => item.status === 0)
      return tmp.length
    }
  }
})
